export const getWeapon = (id?: Nullable<string | number>): string | undefined => {
  if (!notNull(id)) {
    return undefined
  }
  if (!(id.toString() in weapons)) {
    addError({
      type: ValidationErrorType.UNKNOWN_WEAPON,
      what: id.toString()
    })
  }
  return weapons[id] ?? 'kongfu'
}
